<template>
  <div class="new-price-table1">
      <table class="table pulbic-table">
        <thead>
          <tr> 
              <td>{{ $t('translateLang.tradeDetail.text21') }}</td>
              <td>{{ $t('translateLang.tradeDetail.text23') }}</td>
              <td>{{ $t('translateLang.tradeDetail.text42') }}</td>
              <td>{{ $t('translateLang.tradeDetail.text40') }}</td>
            </tr>
        </thead>
        <tbody id="realtime-trade-sell" v-if="plateData.trade_list">
          <tr v-for="item in plateData.trade_list" :key="item.id" >
            <td>{{ item.orders_time.substring(11) }}</td>
            <td class="c-red" v-if="item.trade_type == 'sell'">{{ $t('translateLang.tradeDetail.text5') }}</td><td class="c-green" v-else>{{ $t('translateLang.tradeDetail.text4') }}</td>
            <td class="c-red" v-if="item.trade_type == 'sell'">{{ parseFloat(item.trade_price).toFixed(5) }}</td><td class="c-green" v-else>{{ parseFloat(item.trade_price).toFixed(5) }}</td>
            <td>{{ parseFloat(item.trade_success_num).toFixed(5) }}</td>
          </tr>
          <tr v-if="plateData.trade_list.length == 0"><td colspan="7" valign="top"><div class="no-record zwjl"><img src="/static/images/zwjl.png"> <p>{{ $t('tradeDetail.myOrder.part13')  }}</p></div></td></tr>
        </tbody>
      </table>
  </div>
</template>

<script>
import { entrustCancelOption } from '@/api/trade'
export default {
  props: ['plateData'],
  data() {
    return {
    }
  },
  methods: {
    handleRevoke(orders_id) {
      const data = {
        'currency_trade_id': this.$route.query.trade_area_id,
        'currency_id': this.$route.query.currency_id,
        'orders_id': orders_id
      }
      entrustCancelOption(data).then(res => {
        if (res.status !== 1000) {
          return false
        }
        this.tradeInfo = { ...res.data }
      }).catch(() => {
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .new-price-table1 {
    background: #191b2b;
    font-size: 12px;
    color: #fff;
    height: 764px;
    .title{
      line-height: 40px;
      font-size: 14px;
      color: #b0b8db;
      background: #131625;
      padding-left: 20px;
    }
    .pulbic-table thead tr {
        // background: #2a314e;
        color: #828594;
        line-height: 1.42857143;
    }
    .pulbic-table tr td:first-child {
        padding: 0px 20px !important;
    }
    .pulbic-table tr td:last-child {
        padding: 0px 20px !important;
        text-align: right;
    }
    .pulbic-table tr td {
      vertical-align: middle;
      text-align: left;
      line-height: 32px;
    }
    .pulbic-table tbody tr td{
      color: #b0b8db ;
    }
    .pulbic-table {
      text-align: left;
      font-size: 12px;
    }
    table.pulbic-table tbody tr {
      border-bottom: 1px solid #191b2a;
    }
    .pulbic-table>tbody tr:hover{
      // background: #2a314e;
    }
    
    table {
      border-spacing: 0;
      border-collapse: collapse;
      width: 100%;
      max-width: 100%;
      margin-bottom: 20px;
    }
    .cancel{
      display: inline-block;
      border: 1px solid #37c769;
      width: 50px;
      height: 30px;
      line-height: 30px;
      border-radius: 5px;
      color: #37c769;
      text-align: center;
    }
    a.cancel:focus, a.cancel:hover{
      color: #37c769;
    }
     th{
       color: #61688a !important;
    }
    .zwjl{
      padding: 50px;
      margin: 0 auto;
      display: block;
      text-align: center;
    }
    .zwjl p{
      margin-top: 18px;
      color: #adadad;
    }
    .time-long{
      width: 180px;
    }
  }
</style>



